import React, { useContext, useRef, useState } from 'react';
import { useKeyPressEvent } from 'react-use';
import OverlayContext from 'context/OverlayContext/';
import Button from 'components/Button/';
import { ReactComponent as InfoCircle } from 'media/image/info-circle.svg';
import { ReactComponent as ArrowLeft } from 'media/image/arrow-left.svg';
import { ReactComponent as ArrowRight } from 'media/image/arrow-right.svg';
import playAudioInstance from 'api/playAudioInstance';
import { click3 } from 'media/audio/click/';
import expand from 'media/audio/expand/';
import collapse from 'media/audio/collapse';
import './overlay.css';
import './info.css';
import './backgrounds/noise.css';
import './backgrounds/groovepaper.css';

const string = require('data/string.json');

const BarButton = React.forwardRef(({ children, name, disabled = false, callback, title }, ref) => (
    <button
      className="barButton"
      name={name}
      onMouseDown={() => playAudioInstance(click3)}
      onClick={callback}
      disabled={disabled}
      title={title}
      ref={ref}
    >
      {children}
    </button>
  ));

function Overlay({ visible = true, callbacks = {} }) {
  const { index, lastIndex, scene } = useContext(OverlayContext);
  const [infoVisible, setInfoVisible] = useState(false);
  const [filterEnabled, setFilterEnabled] = useState(false);
  const infoContainer = useRef(null);
  const prevButton = useRef(null);
  const nextButton = useRef(null);

  const showInfo = () => {
    if (!infoVisible) {
      infoContainer.current.scrollTop = 0;
      setInfoVisible(true);
      setFilterEnabled(true);
      callbacks.info();
      playAudioInstance(expand);
    }
  };

  const dismissInfo = () => {
    if (infoVisible) {
      setInfoVisible(false);
      callbacks.dismissInfo();
      playAudioInstance(collapse);
    }
  };

  const toggleInfo = () => {
    if (infoVisible) {
      dismissInfo();
    } else if (!infoVisible && document.activeElement.tagName !== ('INPUT')) {
      showInfo();
    }
  }

  useKeyPressEvent('Escape', dismissInfo);
  useKeyPressEvent('i', toggleInfo);

  return (
    <div className={(visible) ? 'overlay visible' : 'overlay'}>
      {/* info */}
      <aside className={`info groovepaper${(infoVisible) ? ' visible' : ''}`}>
        {/* dismiss info */}
        <Button
          className="dismissButton"
          callback={dismissInfo}
          blurOnInteract
          disabled={(!infoVisible)}
        >
          Dismiss
        </Button>
        <div className="infoContainer" ref={infoContainer}>
          <h1 className="textSemibold textHeading">
            {string[scene].title}
          </h1>
          {string[scene].body.map((paragraph, i) => (
            <p key={i}>{paragraph}</p>
          ))}
        </div>
      </aside>

      {/* bar */}
      <nav className={`bar${(scene === 'inaccessibility') ? ' inaccessible' : ''}`}>
        {/* info toggle */}
        <BarButton
          name="info"
          callback={showInfo}
          disabled={(infoVisible)}
          title="Click or press the “i” key to open"
        >
          <InfoCircle />
          Where am I?
        </BarButton>
        {/* prev/next */}
        <div className="buttonGroup">
          {/* prev */}
          <BarButton name="previous"
            callback={callbacks.prev}
            disabled={(index <= 1 || infoVisible)}
            title="Return to the previous scene"
            ref={prevButton}
          >
            <ArrowLeft />
            Prev
          </BarButton>
          {/* next */}
          <BarButton name="next"
            callback={callbacks.next}
            disabled={(index === lastIndex - 1 || infoVisible)}
            title="Go to the next scene"
            ref={nextButton}
          >
            <ArrowRight />
            Next
          </BarButton>
        </div>
        {/* leave */}
        <Button
          className="leaveButton"
          callback={callbacks.leave}
          disabled={(index === 0 || infoVisible)}
        >
          Leave
        </Button>
      </nav>

      {/* vignette */}
      <div
        className={(infoVisible) ? 'vignette noise dim' : 'vignette noise'}
        onClick={dismissInfo}
      >
        <div
          className={`vignetteFilter${(filterEnabled) ? ' noise enabled' : ''}`}
          onTransitionEnd={() => {
            if (!infoVisible) setFilterEnabled(false);
          }}
        />
      </div>
    </div>
  );
}

export default Overlay;
