import React, { useRef, useState } from 'react';
import Pizzicato from 'pizzicato';
import { useEffectOnce, usePermission } from 'react-use';
import { Sound } from 'pts';
import { QuickStartCanvas } from 'react-pts-canvas';
import Scene from 'components/Scene/';
import audio from 'media/audio/feedback/';
import './grima.css';

function Grima() {
  const volumeController = useRef(null);
  const [sound, setSound] = useState();
  const micPermission = usePermission({ name: 'microphone' });

  const distortion = new Pizzicato.Effects.Distortion({
    gain: 1
  });

  const delay = new Pizzicato.Effects.Delay({
    feedback: 0,
    time: 0.5,
    mix: 0.5
  });

  const mic = new Pizzicato.Sound({
    source: 'input',
    options: {
      attack: 0.5,
      release: 0.9
    }
  }, () => {
    mic.addEffect(delay);
    mic.addEffect(distortion);
    mic.stop();
  });

  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        audio.play();
        mic.play();
        if (JSON.stringify(micPermission, null, 2) && !sound) {
          Sound.input().then((s) => {
            setSound(s.analyze(128, -100, -30, 0.5));
          });
        } else {
          sound.start();
        }
      } else {
        audio.pause();
        mic.pause();
        if (sound && sound.playable) sound.stop();
      }
    });
  }, { threshold: 0.25, rootMargin: '0px -50% 0px 0px' });

  useEffectOnce(() => {
    observer.observe(volumeController.current);
  }, [observer]);

  return (
    <Scene border name="grima">
      <div className="volumeController" ref={volumeController}>
        <QuickStartCanvas
          background="#f5ef4f"
          onAnimate={(space, form, time, ftime) => {
            if (sound && sound.playable) {
              const td = sound.timeDomainTo(space.size);
              form.strokeOnly('#111', 8).line(td);
            }
          }}
        />
      </div>
    </Scene>
  );
}

export default React.memo(Grima);
