import React, { useState, useRef, useCallback, useEffect } from 'react';
import { useKeyPressEvent } from 'react-use';
import OverlayContext from 'context/OverlayContext/';
import Overlay from 'components/Overlay/';
import Entry from 'components/Scene/Entry/';
import SceneList from 'components/Scene/SceneList';
import './App.css';

function App({ children }) {
  const el = useRef(null);

  const [initiated, setInitiated] = useState(false);
  const [lastIndex, setLastIndex] = useState();
  const [sceneList, setSceneList] = useState(['entry']);
  const [scene, setScene] = useState(sceneList[0]);
  const [scroll, setScroll] = useState(0);
  const [scrollEnabled, setScrollEnabled] = useState(false);
  // const [scrollStartTime, setScrollStartTime] = useState(null);

  const scrollRight = useCallback((length = 1) => {
    if (scroll <= -1 * lastIndex) return;
    if (scrollEnabled || scroll === 0) {
      setScrollEnabled(false);
      setScroll(Math.max(scroll - length, -1 * el.current.childNodes.length + 1));
      setTimeout(() => {
        setScrollEnabled(true);
      }, 250);
    }
  }, [lastIndex, scroll, scrollEnabled]);

  const scrollLeft = useCallback((length = 1) => {
    if (scroll >= -1) return;
    if (scrollEnabled) {
      setScrollEnabled(false);
      setScroll(Math.min(scroll + length, -1));
      setTimeout(() => {
        setScrollEnabled(true);
      }, 250);
    }
  }, [scroll, scrollEnabled]);

  // const handleWheel = (event) => {
  //   const currentTime = new Date().getTime();
  //
  //   if (scrollStartTime !== 'undefined') {
  //     const timeDiff = currentTime - scrollStartTime;
  //     const threshold = 40;
  //
  //     if (timeDiff > 250) {
  //       if ((event.deltaY > threshold || event.deltaX > threshold)) {
  //         scrollRight();
  //       } else if ((event.deltaY < threshold * -1 || event.deltaX < threshold * -1)) {
  //         scrollLeft();
  //       }
  //     }
  //   }
  //
  //   setScrollStartTime(currentTime);
  // };

  const handleArrowRight = () => {
    if (document.activeElement.tagName !== 'INPUT') scrollRight();
  };
  const handleArrowLeft = () => {
    if (document.activeElement.tagName !== 'INPUT') scrollLeft();
  };

  const init = () => {
    navigator.mediaDevices.getUserMedia({ video: true, audio: true })
      .then((stream) => {
        setInitiated(true);
        setScrollEnabled(true);
        setLastIndex(el.current.childElementCount);
        setSceneList([...el.current.children].map(i => i.id));
        window.requestAnimationFrame(() => {
          scrollRight();
        });
      })
      .catch((err) => {
        console.log('fuck');
      })
  };

  useKeyPressEvent('ArrowRight', handleArrowRight);
  useKeyPressEvent('ArrowLeft', handleArrowLeft);

  useEffect(() => {
    setScene(sceneList[Math.abs(scroll)]);
  }, [el, sceneList, scroll]);

  return (
    <OverlayContext.Provider value={{
      index: Math.abs(scroll),
      scene,
      lastIndex
    }}>
      <div
        className="App"
        ref={el}
        // onWheel={handleWheel}
        style={{ transform: `translateX(${scroll * 100}vw)` }}
      >
        <Entry callback={init} />
        {initiated && (<SceneList />)}
      </div>
      {initiated && (<Overlay
        callbacks={{
          info() { setScrollEnabled(false); },
          dismissInfo() { setScrollEnabled(true); },
          prev() { scrollLeft(); },
          next() { scrollRight(); }
        }}
        />)}
    </OverlayContext.Provider>
  );
}

export default App;
