import React, { useContext, useState, useEffect } from 'react';
import SceneVisible from 'context/SceneVisible/';
import Scene from 'components/Scene/';
import Eyeball from 'components/Eyeball/';
import playAudioInstance from 'api/playAudioInstance';
import audio from 'media/audio/zoombell/';
import './surveillance.css';

function Video({ animationDelay = 0 }) {
  const [initiated, setInitiated] = useState(false);

  const playAudio = () => {
    if (!initiated) playAudioInstance(audio);
  };

  return (
    <div
      className="videoframe"
      style={{ animationDelay: `${animationDelay}s` }}
      onAnimationStart={() => {
        setInitiated(true);
        playAudio();
      }}
    >
      <div className="videoframeContainer">
        <Eyeball
          openDelay={animationDelay / 5 + Math.random() + 0.6}
          twitchDelay={Math.random() * 3}
        />
      </div>
    </div>
  );
}

function VideoGrid() {
  const sceneVisible = useContext(SceneVisible);
  const items = [];
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    if (sceneVisible) {
      setVisible(true);
    }
  }, [sceneVisible]);


  for (let i = 0; i < 9; i++) {
    items.push(<Video key={i} animationDelay={(i + 0.15) * 0.1} />);
  }

  return (visible) ? (
    <div className="videogrid">
      {items}
    </div>
  ) : '';
}

function Surveillance() {
  return (
    <Scene name="surveillance">
      <VideoGrid />
    </Scene>
  );
}

export default React.memo(Surveillance);
